    title: 'Store Management',
    keywords: [
      { text: 'Studio', role: 'biz', pageIndex: 7, learned: false },
      { text: 'No-Code Builder', role: 'biz', pageIndex: 7, learned: false },
      { text: 'Assets Library', role: 'biz', pageIndex: 7, learned: false },
      { text: 'Offer Management', role: 'biz', pageIndex: 7, learned: false },
      { text: 'Bundles', role: 'biz', pageIndex: 7, learned: false },
      { text: 'Price Points', role: 'biz', pageIndex: 6, learned: false },
      { text: 'Localization', role: 'biz', pageIndex: 7, learned: false },
      { text: 'Segments', role: 'biz', pageIndex: 7, learned: false },
      { text: 'Coupons', role: 'biz', pageIndex: 7, learned: false },
