            <h3 class="font-bold text-slate-800 text-sm uppercase tracking-wider mb-4 pb-2 border-b border-slate-50">
              {{ category.title }}
            </h3>
            
            <div class="flex flex-wrap gap-2">
              <div 
                v-for="kw in category.keywords" 
                :key="kw.text"
                class="group flex items-stretch rounded-lg border text-sm transition-all duration-200 overflow-hidden"
                :class="[
                  getRoleColor(kw.role, !!kw.learned),
                  kw.learned ? 'opacity-75' : 'hover:shadow-sm hover:-translate-y-0.5'
                ]"
              >
                <!-- Main Body: Mark as Learned -->
                <button 
                  class="px-3 py-1.5 flex items-center gap-1.5 text-left outline-none"
                  @click="toggleLearned(kw)"
                >
                  <Check v-if="kw.learned" class="w-3.5 h-3.5" />
                  <span class="font-medium" :class="{ 'line-through': kw.learned }">{{ kw.text }}</span>
                </button>

                <!-- Right Action: Go to Slide -->
                <button 
                  v-if="kw.pageIndex !== undefined"
                  class="px-2 py-1.5 border-l border-black/5 hover:bg-black/5 flex items-center justify-center transition-colors outline-none"
